}`, "",{"version":3,"sources":["webpack://./src/components/Alert/Alert.scss","webpack://./src/styles/_variables.scss"],"names":[],"mappings":"AAAA,gBAAgB;AAEhB;EACI,YAAA;EAEA,gBAAA;EACA,gBAAA;AADJ;AAGI;EACI,eAAA;EACA,mBAAA;EACA,sBCSK;ADVb;AAII;EACI,eAAA;EACA,4BCEM;ADJd;AAKI;EACI,eAAA;EACA,cCWY;ADdpB;AAKQ;EACI,aAAA;AAHZ","sourcesContent":["@import \"../../styles/variables\";\n\n.alert {\n    width: 100vw;\n    // height: 100px;\n    background: grey;\n    text-align: left;\n\n    strong {\n        display: inline;\n        font-weight: normal;\n        color: $color__n-2;\n    }\n\n    p {\n        display: inline;\n        color: $color__n-10;\n    }\n\n    a {\n        display: inline;\n        color: $color__blurple__text--light;\n\n        &:after {\n            content: \" ‚Üí\"\n        }\n    }\n}","// Colors\n// ------------------------------------------------\n\n// Black and white\n$color__white: hsl(0, 0%, 100%);\n$color__black: hsl(240, 20%, 1%);\n\n// Neutral palette\n$color__n-100: hsl(260, 13%, 5%);\n$color__n-90: hsl(251, 16.9%, 12.7%);\n$color__n-85: hsl(249, 17%, 15%);\n$color__n-80: hsl(251, 15.7%, 20%);\n$color__n-70: hsl(250, 16.4%, 21.6%);\n$color__n-60: hsl(249, 14%, 27%);\n$color__n-50: hsl(253, 11.2%, 31.4%);\n$color__n-40: hsl(252, 10.9%, 36.1%);\n$color__n-30: hsl(252, 11.3%, 45.1%);\n$color__n-20: hsl(252, 6.8%, 57.1%);\n$color__n-10: hsl(257, 4.3%, 68.4%);\n$color__n-5: hsl(252, 8.9%, 78%);\n$color__n-2: hsl(0, 0%, 90%);\n\n// Yellow/Medium\n$color__y-5: #ffe878;\n$color__y-1: #ffec91;\n// Blurple palette\n// TODO these colors need hand adjustments\n$color__blurple-80: #4a3dff;\n$color__blurple-70: #5f3dff;\n$color__blurple-60: #7549ff; // button\n$color__blurple-50: #7f5bff; // \n$color__blurple-40: #7f63ef; // button hover, text\n$color__blurple-30: #9385ff; // text light\n$color__blurple-20: #9e99ff;\n\n$color__blurple__text: $color__blurple-40;\n$color__blurple__text--light: $color__blurple-30;\n\n// Green palette\n$color__green: hsl(144, 58%, 59%);\n\n// Red palette\n$color__red: #ff427b;\n\n// Brand colors\n$color__button-blurple: $color__blurple-60;\n$color__button-blurple--hover: $color__blurple-40;\n$color__project-color: #463dff;\n$color__project-color--hover: lighten($color__project-color, 5%);\n\n$color__project: $color__project-color;\n$color__warden: $color__button-blurple;\n\n// Blended colors\n$color__blended-60: #4d22c7;\n\n$color__blended-80: #2c0e83;\n\n// Color Defaults\n// ------------------------------------------------\n\n// Background\n$color__background: $color__n-100;\n\n// Text\n$color__text: $color__n-10;\n$color__headline: $color__white;\n$color__subline: $color__n-10;\n\n// Forms\n$color__form-error: $color__red;\n\n// Buttons\n$color__button-danger: $color__red;\n$color__button-danger--hover: lighten($color__button-danger, 5%);\n\n// üçî TODO add default font sizes and em spacing\n\n// Font Sizes\n// ------------------------------------------------\n\n$font-size__copy: 1.2rem;\n$font-size__copy__code: 0.9em; // I'm also using this for all code blocks\n$font-size__text: 1rem;\n$font-size__text__code: 0.9em;\n$font-size__small: 0.933rem;\n\n$headline-font-size__xxxs: 1rem; // Sublines\n$headline-font-size__xxs: 1.2rem; // H6\n$headline-font-size__xs: 1.3rem; // H5\n$headline-font-size__s: 1.3rem; // H4\n$headline-font-size__m: 1.6rem; // H3\n$headline-font-size__l: 1.8rem; // H2\n$headline-font-size__ll: 2.1rem; // H2\n$headline-font-size__xl: 2.533rem; // H1\n$headline-font-size__xxl: 3.2rem; // Hero headline\n$headline-font-size__xxxl: 3.2rem; // Landing page hero headline\n\n// Font Families\n// ------------------------------------------------\n\n$font__default: \"Satoshi Variable\", sans-serif;\n// üçî TODO add DM sans as body copy font\n$font__code: \"PT Mono\", monospace;\n\n// Spacing\n// ------------------------------------------------\n\n$spacing__base: 0.5rem; // 8px\n\n$spacing__xxs: calc($spacing__base/ 4); // 2px\n$spacing__xs: calc($spacing__base/ 2); // 4px\n$spacing__s: $spacing__base; // 8px\n$spacing__m: calc($spacing__base * 2); // 16px\n$spacing__l: calc($spacing__base * 3); // 24px\n$spacing__xl: calc($spacing__base * 4); // 32px\n$spacing__xxl: calc($spacing__base * 8); // 64px\n$spacing__xxxl: calc($spacing__base * 10); // 80px\n\n// Breakpoints\n// ------------------------------------------------\n\n$breakpoint__xxs: 450px;\n$breakpoint__xs: 600px;\n$breakpoint__s: 750px;\n$breakpoint__m: 960px;\n$breakpoint__l: 1280px;\n$breakpoint__xl: 1920px;\n\n// Layout\n// ------------------------------------------------\n\n$layout__max-width: 1100px;\n$report__max-width: 900px;\n$contest__max-width: 1100px;\n$form__max-width: 900px;\n$newsletter-signup__max-width: 900px;\n\n// Shadows\n// ------------------------------------------------\n\n$shadow__base: 0 0 0.5rem 0 rgba(0, 0, 0, 0.5);\n$shadow__hover: 0 0 0.5rem 0 rgba(0, 0, 0, 0.75);\n\n// Border radius\n// ------------------------------------------------\n\n$border-radius__base: 0.2rem; // 4px\n$border-radius__l: calc($border-radius__base * 2); // 8px\n$border-radius__m: $border-radius__base; // 4px\n$border-radius__s: calc($border-radius__base/ 2); // 2px\n\n// Z-index\n// ------------------------------------------------\n\n$z-index__base: 1;\n$z-index__modal: 1000;\n\n// Media queries\n// ------------------------------------------------\n\n@mixin breakpoint($breakpoint) {\n  $breakpoints: (\n    \"xxs\": $breakpoint__xxs,\n    \"xs\": $breakpoint__xs,\n    \"s\": $breakpoint__s,\n    \"m\": $breakpoint__m,\n    \"l\": $breakpoint__l,\n    \"xl\": $breakpoint__xl,\n  );\n\n  @media (min-width: map-get($breakpoints, $breakpoint)) {\n    @content;\n  }\n}\n\n// Transitions\n// ------------------------------------------------\n\n$transition__base: 0.2s ease-in-out;\n\n// Animations\n// ------------------------------------------------\n\n// Example of using @media to wrap an animation mixin.\n@mixin accessible-animation {\n  @media (prefers-reduced-motion: no-preference) {\n    @content;\n  }\n}\n"],"sourceRoot":""}]);
